.footer {
    background-color: var(--color-black);
    color: white;
    padding: 80px 0 20px;
    border-top: 1px solid #333;
}

.footerContainer {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 40px;
    margin-bottom: 60px;
}

.footerLogo h2 {
    font-size: 32px;
    font-weight: 800;
    letter-spacing: 2px;
}

.footerLinks {
    display: flex;
    gap: 60px;
}

.column h4 {
    margin-bottom: 24px;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.column ul li {
    margin-bottom: 12px;
    font-size: 14px;
    color: #aaa;
    cursor: pointer;
    transition: color 0.3s ease;
}

.column ul li:hover {
    color: white;
}

.copyright {
    text-align: center;
    color: #555;
    font-size: 12px;
    padding-top: 20px;
    border-top: 1px solid #222;
}

/* Responsive */
@media (max-width: 768px) {
    .footerContainer {
        flex-direction: column;
        align-items: flex-start;
    }

    .footerLinks {
        flex-wrap: wrap;
        gap: 40px;
    }
}