.collectionSection {
    padding: 80px 0;
    background-color: var(--color-white);
}

.sectionTitle {
    font-size: clamp(32px, 5vw, 48px);
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 40px;
}

.tabs {
    display: flex;
    gap: 16px;
    flex-wrap: wrap;
    margin-bottom: 50px;
}

.tabBtn {
    background: white;
    border: 1px solid #ddd;
    padding: 10px 24px;
    font-size: 14px;
    cursor: pointer;
    transition: all 0.3s ease;
    font-family: var(--font-primary);
}

.tabBtn:hover,
.activeTab {
    background: var(--color-black);
    color: white;
    border-color: var(--color-black);
}

.productGrid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
}

.productCard {
    /* Layout handled by grid */
}

.imageWrapper {
    position: relative;
    border-radius: 12px;
    overflow: hidden;
    margin-bottom: 16px;
    aspect-ratio: 3/4;
}

.productImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease;
}

.productCard:hover .productImage {
    transform: scale(1.05);
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.productCard:hover .overlay {
    opacity: 1;
}

.actionBtn {
    background: transparent;
    border: 1px solid white;
    color: white;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 12px;
    text-transform: uppercase;
    transition: all 0.2s ease;
    width: 120px;
}

.actionBtn:hover {
    background: white;
    color: black;
}

.buyBtn {
    background: white;
    color: black;
}

.buyBtn:hover {
    background: #f0f0f0;
}

.productInfo {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.productInfo h4 {
    font-size: 14px;
    font-weight: 500;
    color: #555;
    margin: 0;
    max-width: 70%;
}

.price {
    font-weight: 700;
    font-size: 16px;
}

/* Responsive */
@media (max-width: 1024px) {
    .productGrid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 600px) {
    .productGrid {
        grid-template-columns: 1fr;
    }
}