.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: var(--nav-height);
    z-index: 1000;
    background: transparent;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
}

.scrolled {
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.05);
    height: 70px;
    /* Compress slightly on scroll */
}

.navContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

.logo h1 {
    font-size: 24px;
    font-weight: 800;
    letter-spacing: 2px;
    text-transform: uppercase;
    margin: 0;
}

.navMenu {
    display: flex;
    gap: 30px;
}

.navMenu li a {
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    position: relative;
}

.navMenu li a::after {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    bottom: -4px;
    left: 0;
    background-color: var(--color-black);
    transition: width 0.3s ease;
}

.navMenu li a:hover::after {
    width: 100%;
}

.navIcons {
    display: flex;
    gap: 20px;
    align-items: center;
}

.icon {
    font-size: 20px;
    cursor: pointer;
    transition: transform 0.2s ease;
}

.icon:hover {
    transform: scale(1.1);
}

.cartIcon {
    position: relative;
}

.badge {
    position: absolute;
    top: -8px;
    right: -8px;
    background: var(--color-black);
    color: white;
    font-size: 10px;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mobileToggle {
    display: none;
    cursor: pointer;
}

/* Mobile Drawer Styles */
.mobileDrawer {
    position: fixed;
    top: 0;
    left: 0;
    width: 280px;
    height: 100vh;
    background: white;
    z-index: 2000;
    box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1500;
}

.drawerHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
}

.closeBtn {
    cursor: pointer;
}

.mobileLinks {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.mobileLinks li a {
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
}

/* Responsive */
@media (max-width: 768px) {
    .navMenu {
        display: none;
    }

    .mobileToggle {
        display: block;
        font-size: 24px;
        /* Ensuring it's on the left, logo center, icons right is handled by flex justify-between */
    }

    .navContainer {
        /* Since we want menu left, logo center, icons right, we might need specific ordering or absolute positioning for center logo if flex behavior isn't enough */
        /* By default flex justify-between with 3 items works: item1(menu), item2(logo), item3(icons) */
    }

    /* Actually, in current DOM structure: Mobile Toggle, Desktop Menu, Logo, Icons.
     On mobile: Toggle(Block), Menu(None), Logo, Icons.
     Order is: Toggle, Logo, Icons. Flex justify-between will space them: Left, Center, Right. Perfect.
  */
}