.darkSection {
    background-color: var(--color-black);
    color: white;
    padding: 120px 0;
    overflow: hidden;
}

.grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 60px;
    align-items: center;
}

.textContent h2 {
    font-size: clamp(32px, 5vw, 64px);
    line-height: 1;
    font-weight: 800;
    text-transform: uppercase;
    margin-bottom: 32px;
}

.textContent p {
    color: #aaa;
    font-size: 16px;
    line-height: 1.8;
    max-width: 500px;
}

.imageContent {
    position: relative;
    height: 600px;
    border-radius: 20px;
    overflow: hidden;
}

.darkImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.8;
}

/* Responsive */
@media (max-width: 900px) {
    .grid {
        grid-template-columns: 1fr;
        text-align: center;
    }

    .textContent p {
        margin: 0 auto;
    }

    .imageContent {
        height: 400px;
        margin-top: 40px;
    }
}