.marqueeSection {
    background-color: var(--color-black);
    color: var(--color-white);
    padding: 20px 0;
    overflow: hidden;
    white-space: nowrap;
    position: relative;
    z-index: 10;
}

.track {
    display: flex;
    width: 200%;
    /* Ensure enough width for smooth loop */
}

.content {
    display: flex;
    gap: 0;
}

.content h1 {
    font-size: clamp(24px, 4vw, 48px);
    font-weight: 800;
    text-transform: uppercase;
    margin: 0;
    padding: 0 20px;
    display: flex;
    align-items: center;
}

.star {
    color: var(--color-white);
    margin: 0 20px;
    font-size: 0.8em;
}