.categorySection {
    padding: 80px 0;
    background-color: var(--color-white);
}

.gridContainer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
}

.card {
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    height: 500px;
    cursor: pointer;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
}

.imageWrapper {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.catImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.6s cubic-bezier(0.33, 1, 0.68, 1);
}

.card:hover .catImage {
    transform: scale(1.1);
}

.content {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 30px;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.6), transparent);
    color: white;
    transform: translateY(20px);
    transition: transform 0.4s ease;
}

.card:hover .content {
    transform: translateY(0);
}

.content h3 {
    font-size: 24px;
    margin-bottom: 8px;
    font-weight: 600;
}

.content p {
    font-size: 14px;
    opacity: 0.8;
    margin-bottom: 20px;
}

.cardBtn {
    background: white;
    color: black;
    border: none;
    padding: 10px 24px;
    border-radius: 30px;
    font-weight: 600;
    cursor: pointer;
    opacity: 0;
    transform: translateY(20px);
    transition: all 0.4s ease 0.1s;
}

.card:hover .cardBtn {
    opacity: 1;
    transform: translateY(0);
}

/* Responsive */
@media (max-width: 1024px) {
    .gridContainer {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 600px) {
    .gridContainer {
        grid-template-columns: 1fr;
    }

    .card {
        height: 400px;
    }
}