.heroSection {
    padding: 120px 0 60px;
    min-height: 100vh;
    display: flex;
    align-items: center;
    position: relative;
    overflow: hidden;
}

.heroContainer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    align-items: center;
    z-index: 2;
}

.heroContent {
    padding-right: 40px;
}

.heroTitle {
    font-size: clamp(48px, 6vw, 96px);
    /* Responsive font size */
    line-height: 0.9;
    font-weight: 800;
    color: var(--color-black);
    margin-bottom: 24px;
}

.outlinedText {
    -webkit-text-stroke: 2px var(--color-black);
    color: transparent;
    display: block;
}

.heroDescription {
    font-size: 16px;
    color: var(--color-text-gray);
    margin-bottom: 32px;
    max-width: 480px;
    line-height: 1.6;
}

.heroButtons {
    display: flex;
    gap: 16px;
}

.primaryBtn,
.secondaryBtn {
    padding: 14px 28px;
    font-size: 14px;
    font-weight: 600;
    text-transform: uppercase;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease;
}

.primaryBtn {
    background: var(--color-black);
    color: white;
}

.primaryBtn:hover {
    background: #333;
    transform: translateY(-2px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.secondaryBtn {
    background: transparent;
    border: 1px solid var(--color-black);
    color: var(--color-black);
}

.secondaryBtn:hover {
    background: var(--color-black);
    color: white;
}

/* 3D Image Wrapper */
.heroImageWrapper {
    perspective: 1000px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.card3D {
    position: relative;
    width: 100%;
    max-width: 500px;
    border-radius: 24px;
    overflow: hidden;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.15);
    transform-style: preserve-3d;
}

.heroImage {
    width: 100%;
    height: auto;
    display: block;
    transform: scale(1.05);
    /* Avoid white edges during tilt */
}

.glassEffect {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0) 100%);
    pointer-events: none;
    z-index: 2;
}

.floatingTag {
    position: absolute;
    top: 40px;
    right: -20px;
    background: white;
    padding: 8px 16px;
    border-radius: 50px;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    z-index: 5;
}

.bgGradient {
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    background: radial-gradient(circle at 70% 30%, #f5f5f5 0%, transparent 60%);
    z-index: 1;
}

/* Responsive */
@media (max-width: 900px) {
    .heroContainer {
        grid-template-columns: 1fr;
        text-align: center;
    }

    .heroContent {
        padding-right: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .heroImageWrapper {
        margin-top: 40px;
    }
}